import ProjectCard from '@/components/projectCard'; // Adjust path if needed
import { getProjects } from '@/lib/projects'; // Assuming you have this utility
import Link from 'next/link';
import Header from '@/components/header';
import ContactCTASection from "@/components/contactCTASection"; // Or your existing CTA component
import Footer from '@/components/footer';
import { ArrowRight } from "lucide-react";

// Tailored categories for Utility's focus
const CATEGORIES = [
  "All Projects",
  "Mobile Apps",
  "Web Platforms",
  "AI & ML Solutions",
  "Branding & Design",
  "Custom Software",
];

export default function OurWorkPage() {
  const allProjects = getProjects();
  
  if (!allProjects || allProjects.length === 0) {
    return (
      <>
        <Header />
        <main className="min-h-screen bg-white flex flex-col items-center justify-center py-40">
          <p className="text-xl text-gray-500">No projects to display yet. Check back soon!</p>
        </main>
        <ContactCTASection />
        <Footer />
      </>
    );
  }

  const featuredProject = allProjects[0];
  const listedProjects = allProjects.slice(1);

  return (
    <>
      <Header />
      <main className="min-h-screen bg-white">

        {/* Hero Section with Headline & Featured Project */}
        <section className="pt-32 pb-16 md:pt-40 md:pb-24 lg:pb-32">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <header className="max-w-4xl mx-auto mb-12 text-center">
              <h1 className="text-5xl sm:text-6xl md:text-7xl lg:text-8xl font-extrabold text-gray-900 mb-6 leading-tight">
                Our Work.
              </h1>
              <p className="text-xl md:text-2xl text-gray-600 leading-relaxed max-w-3xl mx-auto">
                Award-winning mobile apps, web platforms, and digital products built for startups and global brands like Airbnb, Coca-Cola, Samsung, the NBA, and Forbes.
              </p>
            </header>
          </div>

          {/* Prominent Featured Project */}
          <div className="mt-16 md:mt-24">
            <ProjectCard project={featuredProject} isFeatured={true} />
          </div>
        </section>

        {/* Project Grid with Filters */}
        <section className="py-16 md:py-24 lg:py-32 bg-gray-50">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            
            <div className="mb-12 md:mb-16 flex flex-col md:flex-row md:items-center md:justify-between">
              <h2 className="text-3xl sm:text-4xl font-bold text-gray-900 mb-6 md:mb-0">
                Explore More Projects
              </h2>

              {/* Category Filters (Desktop) */}
              <nav className="hidden md:flex space-x-4">
                {CATEGORIES.map((category) => (
                  <Link
                    key={category}
                    href={`?category=${category.replace(/ /g, '-').toLowerCase()}`}
                    className={`
                      px-6 py-3 text-sm font-medium rounded-full transition-all duration-200
                      ${category === "All Projects"
                        ? "bg-gray-900 text-white"
                        : "bg-white text-gray-700 hover:bg-gray-100 border border-gray-300"
                      }
                    `}
                  >
                    {category}
                  </Link>
                ))}
              </nav>

              {/* Mobile Filter Hint */}
              <div className="md:hidden text-gray-600">
                Filter by category â†’
              </div>
            </div>

            {/* Responsive Grid */}
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8 md:gap-12 lg:gap-16">
              {listedProjects.map((project) => (
                <ProjectCard key={project.slug} project={project} />
              ))}
            </div>

            {/* View All Link if many projects */}
            {allProjects.length > 6 && (
              <div className="text-center mt-16">
                <Link
                  href="/our-work/all" // Or implement pagination
                  className="inline-flex items-center gap-2 text-xl text-gray-900 font-semibold hover:gap-4 transition-all duration-300"
                >
                  View all {allProjects.length} projects
                  <ArrowRight className="w-6 h-6" />
                </Link>
              </div>
            )}
          </div>
        </section>

        {/* Call to Action */}
        <ContactCTASection />

      </main>
      <Footer />
    </>
  );
}